
nav button {
  position: absolute;
  right: 0;
}

section.home .ivu-row {
  padding: 20px 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
section.home .top {
  border-top: 1px solid rgba(0, 0, 0, 0.1);
}
section.home main {
  padding: 0 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
section.home main h2 {
    font-size: 2em;
    margin-bottom: 10px;
}

.card-food.active i, .card-food:hover i {
  color: #0179fe;
}
.card-food::after {
  content: '';
  background-color: rgba(0, 0, 0, 0.1);
  height: 1px;
  width: 94%;
  display: block;
  margin: 0 auto;
}
.card-food .content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 35px 20px 20px 20px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
.card-food .right {
  -ms-flex-preferred-size: 25%;
      flex-basis: 25%;
  width: 30%;
}
.card-food .right img {
    width: 100%;
    max-width: 150px;
}
.card-food .left {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  padding-left: 20px;
}
.card-food .left .input-wrap {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    margin-bottom: 10px;
}
.card-food .left .input-wrap p {
      width: 70px;
      text-align: right;
      line-height: 32px;
      margin-right: 8px;
}
.card-food .left .ivu-input-wrapper {
    width: 100%;
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
}

.image-uploader {
  position: relative;
}
.image-uploader .image {
    height: inherit;
}
.image-uploader .image .img-responsive {
      display: block;
      height: 100%;
      width: 100%;
}

.meal-content {
  border-radius: 5px;
  background-color: #fff;
  margin: 10px;
  padding: 10px;
}
.meal-content .d1 {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 100%;
}
.meal-content .d1:last-child .e1::after {
      display: none;
}
.meal-content .e1 {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
    position: relative;
    width: 50%;
    padding: 15px 10px;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
.meal-content .e1::after {
      content: '';
      position: absolute;
      display: block;
      height: 1px;
      width: calc(100% - 20px);
      bottom: 0;
      background-color: rgba(0, 0, 0, 0.1);
}

.ivu-card-body h3 {
  display: inline-block;
}
.ivu-card-body::after {
  content: '>';
  display: block;
  position: absolute;
  right: 10px;
  top: 15px;
}
